.addTasks {
  display: flex;
  justify-content: center;
  height: 100px;
  .firstInput {
    display: flex;
    input {
      // display: block;
      padding: 10px 20px;
      letter-spacing: 0.1em;
      width: 428px;
      font-weight: 480px;
      font-size: 14px;
      line-height: 16px;
      opacity: 0.4;
      border: 2px solid #356ead;
      box-sizing: border-box;
      border-radius: 10px;
      margin-top: 8px;
      height: 50px;
    }
    button {
      text-decoration: none;
      outline: none;
      display: inline-block;
      display: flex;
      width: 150px;
      height: 50px;
      line-height: 15px;
      border-radius: 45px;
      margin: 10px 15px;
      font-family: "Montserrat", sans-serif;
      font-size: 11px;
      text-transform: uppercase;
      text-align: center;
      align-content: center;
      align-items: center;
      justify-content: center;
      letter-spacing: 3px;
      font-weight: 600;
      color: #524f4e;
      background: #ffffff;
      box-shadow: 0 8px 15px rgba(0, 0, 0, 0.1);
      transition: 0.3s;
    }
  }
}

